.algwcwishlistmodal {
	background: #fff;
	box-sizing: border-box;
	display: none;
	position: relative;
	padding: 20px;
	max-width: 500px;
	width: 100%;
	vertical-align: middle;
	z-index: 10000;

	&.is-open {
		display: inline-block;
	}
}

.algwcwishlistmodal-overlay {
	background: rgba(0, 0, 0, 0.5);
	display: none;
	position: fixed;
	top: -100%;
	bottom: -100%;
	left: -100%;
	right: -100%;
	cursor: pointer;

	&.is-open {
		display: block;
		z-index: 9999;
	}
}

.algwcwishlistmodal-container {
	box-sizing: border-box;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 10000;
	width: 100%;
	height: 100%;
	display: none;
	text-align: center;
	overflow: auto;
	padding: 10px 10px 0;

	&.is-open {
		display: block;
	}

	&::after {
		content: '';
		display: inline-block;
		margin-left: -0.05em;
		height: 100%;
		vertical-align: middle;
	}

	.iziToast-close {
		background: url(data:image/png;base64,)
		no-repeat 50% 50%;
		background-size: 8px;
		position: absolute;
		top: 0;
		right: 0;
	}

	.form-field-wrap {
		width: 100%;

		label {
			text-align: left;
			width: 100%;
			display: inline-block;
		}

		.form-field {
			width: 100%;
			margin-bottom: 10px;
		}
	}

	.create-wishlist-form.is-hidden,
	.copy-wishlist-form.is-hidden,
	.select-wishlist.is-hidden {
		display: none;
	}
}

/* Checkbox styles */

.algwcwishlistmodal-checkbox-wrapper {
	user-select: none;
	text-align: left;

	*,
	::before,
	::after {
		box-sizing: border-box;
	}

	label {
		display: inline-block;
		cursor: pointer;
	}

	input[type='checkbox'] {
		display: none;
		visibility: hidden;

		&:checked + .cbx {
			background-color: #606062;
			background-image: linear-gradient(#255cd2, #1d52c1);

			svg {
				stroke-dashoffset: 0;
				transition: all 0.15s ease;
			}
		}
	}

	.cbx {
		position: relative;
		display: block;
		float: right;
		width: 18px;
		height: 18px;
		border-radius: 4px;
		background-color: #6060625e;
		background-image: linear-gradient(#47474936, #6060625e);
		box-shadow: inset 0 1px 1px rgba(255, 255, 255, 0.15),
		inset 0 -1px 1px rgba(0, 0, 0, 0.15);
		transition: all 0.15s ease;

		svg {
			position: absolute;
			top: 3px;
			left: 3px;
			fill: none;
			stroke-linecap: round;
			stroke-linejoin: round;
			stroke: #fff;
			stroke-width: 2;
			stroke-dasharray: 17;
			stroke-dashoffset: 17;
			transform: translate3d(0, 0, 0);
		}

		+ span {
			float: left;
			margin-left: 6px;
		}
	}

	span.titlebox {
		font-size: 14px;
		color: #333;
		padding-left: 15px;
	}
}

ul.algwc-wishlist-collections-wrapper {
	margin-left: 0;

	li {
		list-style-type: none;
		width: 100%;
		padding: 5px;
		border: 2px solid #eee;
		margin-bottom: 5px;
		border-radius: 5px;
		background: #eee;

		label {
			float: right;
		}
	}

	&.float-clear {
		clear: both;
	}
}

.button-split {
	text-align: left;

	.page__btn--save {
		float: right;
	}
}
